@media (max-width: 1023px) {
  .site-pages__list {
    grid-template: auto/220px;
    grid-auto-flow: row;
  }
}
@media (max-width: 767px) {
  .site-pages__list {
    grid-row-gap: 10px;
  }
}

.location {
  position: relative;
  max-width: 345px;
  width: auto;
  min-width: 275px;
}
@media (max-width: 1023px) {
  .location {
    width: 325px;
  }
}
@media (max-width: 767px) {
  .location {
    width: 100%;
    max-width: none;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.location--close .location__button {
  background-image: url(../../img/svg/accordion-open.svg);
}

.location--open .location__button {
  background-image: url(../../img/svg/accordion-close.svg);
}

.location--no-js .location__button {
  display: none;
}

@media (max-width: 767px) {
  .location--open .location__title {
    margin-bottom: 15px;
  }
}

@media (max-width: 767px) {
  .location--no-js .location__title {
    margin-bottom: 15px;
  }
}

@media (max-width: 767px) {
  .location--close .location__title {
    margin-bottom: 0;
  }
}

@media (max-width: 767px) {
  .location__item {
    display: flex;
  }
}

@media (max-width: 767px) {
  .location--close .location__list {
    display: none;
  }
}

@media (max-width: 767px) {
  .location--open .location__list, .location--no-js .location__list {
    display: grid;
  }
}

@media (max-width: 767px) {
  .location__text--address {
    margin-top: 2px;
    margin-bottom: 8px;
  }
}

.location__list {
  display: grid;
  grid-template-columns: auto;
  grid-gap: 8px;
}
@media (max-width: 767px) {
  .location__list {
    grid-gap: 10px;
  }
}

.location__item {
  display: flex;
  max-width: 345px;
  width: auto;
  min-width: 275px;
}
@media (max-width: 1023px) {
  .location__item {
    max-width: none;
    min-width: auto;
    width: 325px;
  }
}
@media (max-width: 767px) {
  .location__item {
    width: 270px;
  }
}

@media (max-width: 767px) {
  .location__link {
    font-size: 14px;
    line-height: 20px;
  }
}

